/* eslint-disable @typescript-eslint/no-explicit-any */
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, ElementRef, OnInit, PipeTransform, TemplateRef, ViewChild, inject } from '@angular/core';
import { FormGroup, FormsModule, ReactiveFormsModule } from '@angular/forms';
import { FormlyFormOptions, FormlyFieldConfig, FormlyModule } from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { ColumnMode, NgxDatatableModule, TableColumn } from '@swimlane/ngx-datatable';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { IComLibsServicesInventoryService, IComLibsServicesProductService } from "@icom/services/projects";
import { lastValueFrom, map } from 'rxjs';
import { NgForOf, NgIf, NgSwitch, NgSwitchCase } from '@angular/common';
import { NzTypographyModule } from 'ng-zorro-antd/typography';
import { NzIconModule } from 'ng-zorro-antd/icon';
import { IInventory, IPaginationData, TYPE_TEMPLATE_REF } from '@icom/interfaces';
import * as dayjs from 'dayjs';
import { NzModalModule } from 'ng-zorro-antd/modal';
import { NzSelectModule } from 'ng-zorro-antd/select';
import { NzDropDownModule } from 'ng-zorro-antd/dropdown';
import { NzModalService } from 'ng-zorro-antd/modal';
import { IComMessageService } from '@icom/services/common';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';
import { IcomLibsInventoryAddEditComponent } from '../add-edit/add-edit.component';
import { IcomLibsCoreTableComponent } from '@icom/components/table';
import { IcomLibsHasPermissionDirective } from '@icom/directive';
import { PermissionService } from '@icom/services/authenticate';

@Component({
  selector: 'icom-libs-product-list',
  templateUrl: './list.component.html',
  standalone: true,
  changeDetection: ChangeDetectionStrategy.OnPush,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NzFormModule,
    NgForOf,
    NgSwitch,
    NgSwitchCase,
    NzTypographyModule,
    NzIconModule,
    NzModalModule,
    NzSelectModule,
    FormsModule,
    NzDropDownModule,
    IcomLibsInventoryAddEditComponent,
    NgIf,
    IcomLibsCoreTableComponent,
    IcomLibsHasPermissionDirective
  ],
})
export class IcomLibInventoryListComponent implements OnInit {
  protected form = new FormGroup({});
  protected model: any = {};
  protected pagination!: IPaginationData;
  protected options: FormlyFormOptions = {};
  protected rows: any[] = [];
  protected loadingIndicator = true;
  protected columns: any[];
  protected ColumnMode = ColumnMode;
  protected readonly headerHeight = 50;
  protected readonly rowHeight = 50;
  protected readonly pageLimit = 10;
  protected isVisible!: boolean;
  private eventFunctionControl: any;
  protected isOkLoading!: boolean;

  protected inventoryService = inject(IComLibsServicesInventoryService)
  protected el = inject(ElementRef);
  protected modalService = inject(NzModalService);
  protected IComMessageService = inject(IComMessageService);
  protected router = inject(Router);
  protected cdr = inject(ChangeDetectorRef);
  protected permissionService  = inject(PermissionService);
  
  @ViewChild(IcomLibsInventoryAddEditComponent) addEditComp!: IcomLibsInventoryAddEditComponent;

  constructor() {
    this.columns = [];
  }
  async ngOnInit() {
    this.columns = [
      { title: 'Name', key: 'name', type: 'text' },
      { title: 'Type', key: 'type', type: 'text' },
      { title: 'Rent Start', key: 'start_time', type: 'text', pipe: 'time' },
      { title: 'Rent End', key: 'end_time', type: 'text', pipe: 'time' },
      {
        title: 'Status', 
        key: 'status', 
        pipe: 'array',
        type: 'text',
        pipeArgs: [
          { value: 'Closed', id: 0, class: 'text-gray-500' },
          { value: 'Opened', id: 1, class: 'text-green-500' },
        ]
      },
      {
        title: 'Action',
        key: 'actions',
        type: 'action',
        actions: [
          {
            name: 'Edit',
            callback: this.handlerViewDetail.bind(this),
            condition: () => {
              return this.permissionService.getPermission('edit');
            }
          },
          {
            name: 'Remove',
            callback: this.handlerRemove.bind(this),
            condition: () => {
              return this.permissionService.getPermission('delete');
            }
          },
        ],
      },
    ];
  }

  loadData = (params: any) => {
    return this.inventoryService.listInventory(params);
  }
  
  protected showModal(): void {
    this.isVisible = true;
  }

  protected handleOk(): void {
    const event = {
      success: () => {
        this.isVisible = false;
      },
      isLoading: () => {
        this.isOkLoading = true;
      },
      loadDone: () => {
        this.isOkLoading = false;
        this.eventFunctionControl.reset();
      }
    }
    this.addEditComp.handlerSave(undefined, event);
  }

  protected handleCancel(): void {
    this.isVisible = false;
  }


  protected handlerRemove(row: IInventory) {
    this.modalService.confirm({
      nzTitle: 'Are you sure delete this inventory?',
      nzContent: `<b style="color: red;">${row.name}</b>`,
      nzOkText: 'Delete',
      nzOkType: 'primary',
      nzOkDanger: true,
      nzMaskClosable: false,
      nzClosable: false,
      nzOnOk: this.processDeleteInventory.bind(this, row),
      nzCancelText: 'Cancel'
    }, 'error');
  }

  protected handlerViewDetail(row: IInventory) {
    this.router.navigate(['/inventory/detail', row.id]);
  }

  private processDeleteInventory(row: IInventory) {
    return lastValueFrom(this.inventoryService.deleteInventory(row.id || '')).then(() => {
      this.IComMessageService.showAlertMessageDone('Delete inventory success');
      this.eventFunctionControl.reset();
    }).catch((err) => {
      this.IComMessageService.showAlertMessageError((err as HttpErrorResponse)?.error?.error_message);
    });
  }


  handlerFunctionControl(event: any) {
    this.eventFunctionControl = event;
  }
}
