/* eslint-disable @typescript-eslint/no-explicit-any */
import { ChangeDetectionStrategy, ChangeDetectorRef, Component, Input, OnInit, inject } from '@angular/core';
import { FormGroup, ReactiveFormsModule } from '@angular/forms';
import { IInventory, IProduct, TYPE_FORM_CONTROL } from '@icom/interfaces';
import { IComLibsServicesInventoryService, IComLibsServicesProductService } from '@icom/services/projects';
import {
  FormlyFormOptions,
  FormlyFieldConfig,
  FormlyModule,
} from '@ngx-formly/core';
import { FormlyNgZorroAntdModule } from '@ngx-formly/ng-zorro-antd';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { ConfigFormly } from './defines/formly.define';
import { Observable, lastValueFrom } from 'rxjs';
import { HttpErrorResponse } from '@angular/common/http';
import { parseDateRangeToTwoKey } from '@icom/defines';
import { cloneDeep } from 'lodash';
import { IComMessageService } from '@icom/services/common';
import { NgIf } from '@angular/common';

@Component({
  // eslint-disable-next-line @angular-eslint/component-selector
  selector: 'icom-libs-inventory-add_edit',
  templateUrl: './add-edit.component.html',
  standalone: true,
  imports: [
    FormlyNgZorroAntdModule,
    ReactiveFormsModule,
    FormlyModule,
    NzButtonModule,
    NgIf
  ],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class IcomLibsInventoryAddEditComponent implements OnInit {
  protected form = new FormGroup({});
  protected options: FormlyFormOptions = {};
  protected fields: FormlyFieldConfig[] = [];
  protected isLoading!: boolean;
  protected iComMessageService = inject(IComMessageService);
  protected modelOrigin: IInventory | undefined;

  @Input() model: IInventory | undefined;
  @Input() isAdd!: boolean;
  
  protected iComLibsServicesInventoryService = inject(IComLibsServicesInventoryService);
  protected productService = inject(IComLibsServicesProductService);
  protected changeDetectorRef = inject(ChangeDetectorRef);
  
  constructor() {
    this.model = {};
  }

  ngOnInit() {
    this.fields = ConfigFormly(this.productService);
    this.modelOrigin = cloneDeep(this.model);
  }

  public async handlerSave(e?: Event, event?: any) {
    e?.stopPropagation();
    e?.preventDefault();
    if (!this.form.valid) {
      Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
        (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
      });
      return;
    }
    try {
      this.form.markAsTouched();
      (this.form as TYPE_FORM_CONTROL).submitted = true;
      event && event.isLoading && event.isLoading(); 
      const model = cloneDeep(this.model);
      this.isLoading = true;
      if (this.isAdd) {
        parseDateRangeToTwoKey(model, 'rent_from,rent_to');
        await lastValueFrom(this.iComLibsServicesInventoryService.createInventory(model));
        event?.success && event?.success();
        this.iComMessageService.showAlertMessageDone('Create inventory success');
      }


      else {
      	await lastValueFrom(this.iComLibsServicesInventoryService.updateInventory(this.model?.id, model));
        event &&	event.success && event.success();
        this.iComMessageService.showAlertMessageDone('Edit inventory success');
        this.modelOrigin = cloneDeep(this.model);
      }
    } catch (err) {
      event && event.loadDone && event.loadDone(); 
      this.iComMessageService.showAlertMessageError( (err as HttpErrorResponse)?.error?.error_message || 'Create inventory fail');
    } finally {
      event &&  event.loadDone && event.loadDone(); 
      this.isLoading = false;
      this.changeDetectorRef.detectChanges();
    }
  }

  protected handlerCancel(e: Event) {
    e.stopPropagation();
    this.model = cloneDeep(this.modelOrigin);
}
}
