import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'icomCampaignDetailMissionParseValue',
  standalone: true
})
export class IcomCampaignDetailMissionParseValue implements PipeTransform {

  transform(value: any, listData?: any[]): any {
    if(!listData || !listData.length) {
      return value || '';
    }
    return listData.find(item => item.id === value) || {};
  }

}
