import { Component, EventEmitter, Input, OnInit, Output, inject } from '@angular/core';
import { FormGroup, FormsModule, ReactiveFormsModule } from '@angular/forms';
import { ISectionConfig, TYPE_FORM_CONTROL } from '@icom/interfaces';
import { IComLibsServicesProjectFlashSaleService } from '@icom/services/projects';
import { FormlyFieldConfig, FormlyFormOptions, FormlyModule } from '@ngx-formly/core';
import { NzButtonModule } from 'ng-zorro-antd/button';
import { NzFormModule } from 'ng-zorro-antd/form';
import { NzInputModule } from 'ng-zorro-antd/input';
import { NZ_MODAL_DATA, NzModalModule } from 'ng-zorro-antd/modal';
import { lastValueFrom } from 'rxjs/internal/lastValueFrom';

@Component({
  selector: 'app-add-edit-flash-sale-modal',
  templateUrl: './modal.component.html',
  standalone: true,
  imports: [
    NzModalModule,
    NzFormModule,
    FormsModule,
    NzInputModule,
    NzButtonModule,
    FormlyModule,
    ReactiveFormsModule,
  ]
})
export class AddEditFlashSaleModalComponent implements OnInit {
  protected fields: FormlyFieldConfig[] = [];
  protected form = new FormGroup({});
  protected isLoading!: boolean;
  protected options: FormlyFormOptions = {};
  protected model: ISectionConfig | undefined;

  public readonly nzModalData = inject(NZ_MODAL_DATA);
  
  protected iComLibsServicesProjectFlashSaleService = inject(
    IComLibsServicesProjectFlashSaleService
  );

  @Input() title: string | undefined;
  @Input() data: any | undefined;

  @Output() onSave = new EventEmitter<any>(); 
  
  constructor() {
    this.data = {
      name: ''
    }
    this.fields = [
      {
        key: 'name',
        type: 'input-custom',
        props: {
          label: 'Tên nhóm',
          required: true,
          maxLength: 20,
          type: 'text',
          placeholder: 'Nhập tên nhóm',
        },
        validators: {
          validation: [
            { name: 'noWhitespace', options: { errorPath: 'value' } },
          ],
        },
      },
    ]
  }

  ngOnInit() {
    this.data = this.nzModalData.data;
    this.title = this.nzModalData.title;
  }

  async handlerSave() {
    if (!this.form.valid) {
      Object.keys(this.form.controls as TYPE_FORM_CONTROL).forEach((key) => {
        (this.form.controls as TYPE_FORM_CONTROL)[key].touched = true;
      });
      this.onSave.emit(false)
      return;
    }
    this.isLoading = true;
    
    try {
      let apiResult;

      if (this.data?._id) {
        // Gọi API để cập nhật dữ liệu
        apiResult = await lastValueFrom(this.iComLibsServicesProjectFlashSaleService.addGroup(this.data));
        this.onSave.emit(apiResult);
      } else {
        // Gọi API để thêm mới dữ liệu
        apiResult = await lastValueFrom(this.iComLibsServicesProjectFlashSaleService.editGroup(this.data));
        this.onSave.emit(apiResult);
      }
    } catch (error) {
      // Xử lý lỗi
      this.onSave.emit(error); // Phát ra sự kiện lưu thất bại
    } finally {
      this.isLoading = false;
    }
  }
  
}
